import * as React from "react";
import { ThemeProvider as MuiThemeProvider, createTheme, CssBaseline } from "@mui/material";
import createCache from '@emotion/cache';
import { CacheProvider } from '@emotion/react';
import { ReactNode, useState } from 'react';
import { useServerInsertedHTML } from 'next/navigation';

const theme = createTheme({
  palette: {
    mode: "light",
    primary: {
      main: "#1976d2", // MUI blue
    },
    secondary: {
      main: "#00bcd4", // teal accent
    },
    background: {
      default: "#f5f6fa",
      paper: "#fff",
    },
    text: {
      primary: "#222",
      secondary: "#555",
    },
  },
  shape: {
    borderRadius: 12,
  },
  typography: {
    fontFamily: 'Roboto, Inter, Arial, sans-serif',
    h1: { fontWeight: 700 },
    h2: { fontWeight: 700 },
    h3: { fontWeight: 700 },
    h4: { fontWeight: 700 },
    h5: { fontWeight: 700 },
    h6: { fontWeight: 700 },
    button: { textTransform: 'none', fontWeight: 600 },
  },
  components: {
    MuiCard: {
      styleOverrides: {
        root: {
          boxShadow: '0 2px 12px 0 rgba(0,0,0,0.04)',
          borderRadius: 16,
        },
      },
    },
    MuiButton: {
      styleOverrides: {
        root: {
          borderRadius: 8,
        },
      },
    },
  },
});

// Server-compatible Emotion + MUI provider to avoid hydration mismatches
export function MuiThemeProviderCustom({ children }: { children: ReactNode }) {
  const [{ cache, flush }] = useState(() => {
    const cache = createCache({ key: 'mui', prepend: true });
    let inserted: string[] = [];
    const prevInsert = cache.insert;
    const wrappedInsert: any = (...args: any[]) => {
      const [selector, serialized, sheet, shouldCache] = args as [string, any, any, boolean];
      if (cache.inserted[serialized.name] === undefined) {
        inserted.push(serialized.name);
      }
      // @ts-ignore - pass through to original insert
      return prevInsert(selector, serialized, sheet, shouldCache);
    };
    // @ts-ignore assign wrapped insert
    cache.insert = wrappedInsert;
    const flush = () => {
      const prev = inserted;
      inserted = [];
      return prev;
    };
    return { cache, flush };
  });

  useServerInsertedHTML(() => {
    const names = flush();
    if (names.length === 0) return null;
    let styles = '';
    for (const name of names) {
      styles += cache.inserted[name];
    }
    return (
      <style
        key={cache.key + names.join('-')}
        data-emotion={`${cache.key} ${names.join(' ')}`}
        dangerouslySetInnerHTML={{ __html: styles }}
      />
    );
  });

  return (
    <CacheProvider value={cache}>
      <MuiThemeProvider theme={theme}>
        <CssBaseline />
        {children}
      </MuiThemeProvider>
    </CacheProvider>
  );
}
